@import url('https://fonts.googleapis.com/css2?family=Allison&family=Noto+Sans+TC:wght@300&display=swap');

.header-hm {
  background-color: #D77A5B;
  font-family: 'Noto Sans TC', sans-serif;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-hm h2 {
  margin: 5px 20px 5px 30px;
  cursor: pointer;
}

.header-hm h2:hover {
  color: #050517;
}

.nav-hm {
  margin: 5px 20px 5px 20px;
}

@keyframes nav {
  
}

.nav-hm  svg {
  margin: 0px 10px 0px 10px;
  cursor: pointer;
  transition: transform 0.25s, color 0.25s;
}

.nav-hm  svg:hover {
  color: #050517;
  transform: scale(1.5);
}

.title-link {
  text-decoration: none;
  color: white;
}

@media screen and ( max-width : 500px ) and ( max-height : 850px ) {
  .header-hm {
    background-color: #D77A5B;
    font-family: 'Noto Sans TC', sans-serif;
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .header-hm h2 {
    margin: 10px 10px 10px 20px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .header-hm h2:hover {
    color: #050517;
  }
  
  .nav-hm {
    margin: 10px 10px 10px 20px;
  }
  
  @keyframes nav {
    
  }
  
  .nav-hm  svg {
    margin: 0px 10px 0px 10px;
    cursor: pointer;
    transition: transform 0.25s, color 0.25s;
  }
  
  .nav-hm  svg:hover {
    color: #050517;
    transform: scale(1.5);
  }
  
  .title-link {
    text-decoration: none;
    color: white;
  }
}